<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示所有购买服务的订单信息。</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>服务类型</label>
        <el-select v-model="queryParams.promoteType" @change="query">
          <el-option label="全部" value=""></el-option>
          <el-option label="单品折扣" :value="1"></el-option>
          <el-option label="单品一口价" :value="2"></el-option>
          <el-option label="满减" :value="3"></el-option>
          <el-option label="优惠券" :value="4"></el-option>
          <el-option label="满赠" :value="5"></el-option>
          <el-option label="包邮" :value="6"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>流水号</label>
        <el-input v-model="queryParams.orderNum" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>购买时间</label>
        <cms-picker @change="setTimer"></cms-picker>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column prop="promoteType" label="服务项目" min-width="120px">
        <template slot-scope="scope">
          <span v-if="scope.row.promoteType==1">单品折扣</span>
          <span v-if="scope.row.promoteType==2">单品一口价</span>
          <span v-if="scope.row.promoteType==3">满减</span>
          <span v-if="scope.row.promoteType==4">优惠券</span>
          <span v-if="scope.row.promoteType==5">满赠</span>
          <span v-if="scope.row.promoteType==6">包邮</span>
        </template>
      </el-table-column>
      <el-table-column prop="store.storeName" label="店铺名称" min-width="140"></el-table-column>
      <el-table-column prop="couponType" label="购买时长" align="center" width="120">
        <template slot-scope="scope">
          <span v-if="scope.row.buyServiceTime ===1">3个月</span>
          <span v-if="scope.row.buyServiceTime ===2">6个月</span>
          <span v-if="scope.row.buyServiceTime ===3">1年</span>
          <span v-if="scope.row.buyServiceTime ===4">2年</span>
        </template>
      </el-table-column>
      <el-table-column label="成交价格">
        <template slot-scope="scope">
          <span>{{$cms.float(scope.row.orderAmount)}}元</span>
        </template>
      </el-table-column>
       <el-table-column label="支付方式">
        <template slot-scope="scope">
          <span v-if="scope.row.oderMode==1">支付宝</span>
          <span v-if="scope.row.oderMode==2">微信</span>
          <span v-if="scope.row.oderMode==3">银联</span>
          <span v-if="scope.row.oderMode==4">余额</span>
        </template>
      </el-table-column>
     <el-table-column label="支付状态">
        <template slot-scope="scope">
          <span v-if="scope.row.payStatus==0" class="error">未支付</span>
          <span v-else class="success">已支付</span>
        </template>
      </el-table-column>
      <el-table-column prop="orderNum" label="订单流水号" min-width="180">
      </el-table-column>
      <el-table-column prop="createTime" label="购买时间" min-width="180">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import { promoteServiceOrderPage } from '@/api/marketing/index.js'
import list from '@/mixins/list'
var defaultData = {
  promoteType: '',
  storeName: '',
  orderNum: '',
  timeBegin: '',
  timeEnd: '',
  timer: [],
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    setTimer(value) {
      this.queryParams.timeBegin = value[0]
      this.queryParams.timeEnd = value[1]
      this.getTableData(promoteServiceOrderPage, this.queryParams)
    }
  },
  created() {
    this.getTableData(promoteServiceOrderPage, this.queryParams)
  }
}
</script>

<style>
</style>



